import React from "react";
import guest32px from '../static/guest_32px.png'
import firebase32px from '../static/firebase_32px.png'
import google64px from '../static/google_32px.png'

export const Profile = () => {

    const Button = ({  imgPath , label }) => (
        <button className="auth-btn">
            <img src={imgPath} alt="" />
            {label}
        </button>
    )

    return (
        <h2 id="profile">
            <img src={guest32px} alt="" /> Guest User
            <br />
            <span className="spanLeft"> Signin
                <Button imgPath={google64px} />
                <Button imgPath={firebase32px} />
            </span>
        </h2>
    )
}