import React, { useState, useContext } from "react";
import { GlobalContext } from '../context/GlobalState'

// import React from 'react';
import { auth, provider } from '../auth/Firebase';
import { signInWithPopup, GoogleAuthProvider } from "firebase/auth";
import { useAuthState } from 'react-firebase-hooks/auth';


export const AddTransaction = () => {
    const [text, setText] = useState('');
    const [amount, setAmount] = useState(0);

    const { addTransaction } = useContext(GlobalContext);

    const addItems = () => {
        // e.preventDefault();

        const newTransaction = {
            id: Math.floor(Math.random() * 100000000),
            text,
            amount: +amount
        }

        addTransaction(newTransaction);
    }



    // Google Auth

    // Signout function
    const logout = () => {
        auth.signOut();
    }
    // Sign in with google
    const signin = () => {
        // auth.signInWithPopup(provider).catch(alert);
        signInWithPopup(auth, provider)
            .then((result) => {
                // This gives you a Google Access Token. You can use it to access the Google API.
                // const credential = GoogleAuthProvider.credentialFromResult(result);

                // const credential = provider.credentialFromResult(result);
                // const token = credential.accessToken;

                // The signed-in user info.
                const user = result.user;
                // ...
                console.log(user);
            }).catch((error) => {
                // Handle Errors here.
                const errorCode = error.code;
                const errorMessage = error.message;
                // The email of the user's account used.
                const email = error.customData.email;
                // The AuthCredential type that was used.
                const credential = GoogleAuthProvider.credentialFromError(error);
                // ...
                console.log('MD: ' + errorMessage);
            });

    }

    const [user] = useAuthState(auth);

    return (
        <>
            <h3>Add new items</h3>
            <form onSubmit={(e)=> e.preventDefault()}>
                <div className="form-control">
                    <label htmlFor="text">Item or PayCard</label>
                    <input type="text" value={text} onChange={(e) => setText(e.target.value)} placeholder="Enter text..." />
                </div>
                <div className="form-control">
                    <label htmlFor="amount"> Cost (* Add - as Item, +  as PayCard *)</label>
                    <input type="number" value={amount} onChange={(e) => setAmount(e.target.value)} placeholder="Enter amount..." />
                </div>

                {user ? <div><button className="btn" onClick={addItems}>Add Item</button> <button className="btn-signout" onClick={logout}>Sign Out</button></div> :
                    <button className="btn-google" onClick={signin}>Sign In with Google To Add Items</button>}

            </form>
            <h3>{text}</h3>
        </>
    )
}


